<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>

    <!-- Navbar start -->
    <Navbar @changeLanguage="changCurrentLanguage"/>
    <!-- Navbar end -->

    <!-- Banner start -->
    <div class="product-banner bg-setting banner-img-filter">
      <h3 class="banner-title">{{ $t('ProductBanner')}}</h3>
    </div>
    <!-- Banner end -->

    <!-- Content start -->
    <section class="section">
      <div class="container">
        <div class="row">
          <div class="col-md-3 mobile-hide">
            <SideBar/>
          </div>
          <div class="col-md-9 col-sm-12">
            <Breadcrumb/>
            <router-view></router-view>
          </div>
        </div>
      </div>
    </section>
    <!-- Content end -->

    <!-- Footer start -->
    <Footer/>
    <!-- Footer end -->
  </div>
</template>
<script>
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import SideBar from '../components/SideBar';
import Breadcrumb from '../components/Breadcrumb';
import { Circle4 } from 'vue-loading-spinner'

export default {
  name: 'Product',
  components:{
    Navbar,
    Footer,
    SideBar,
    Breadcrumb,
    Circle4
  },
  data(){
    return{
      isLoading: false,
      currentLanguage: ''
    }
  },
  provide(){
    return{
      currentLang: () => this.currentLanguage
    }
  },
  methods: {
    changCurrentLanguage(val){
      this.currentLanguage = val
      switch(val) {
        case 'en':
          this.$store.commit('setLang', val);
          this.$i18n.locale = val;
          localStorage.setItem('footmark-lang', val);
          break;
        default:
          this.$store.commit('setLang', val);
          this.$i18n.locale = val;
          localStorage.setItem('footmark-lang', val);
          break;
      }
    }
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.product-banner {
  width: 100%;
  height: 400px;
  background-image: url('../assets/images/productBanner.jpg');
  background-position: 0 80% !important;
}
.mobile-hide {
  @include screen($mobile){
    display: none;
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>